* {
  box-sizing: border-box;
  font: inherit;
  margin: 0;
  padding: 0;
}

.layout {
  grid-template-areas: "header header"
                       "input output";
  gap: 10px;
  padding: 10px;
  display: grid;
}

.header {
  grid-area: header;
}

.input {
  grid-area: input;
}

.output {
  grid-area: output;
}

:is(.input, .output) textarea {
  width: 100%;
  height: 70vh;
  padding: .25em .5em;
}

